// src/scss/_animations.scss
//       @keyframes    .

//    variables     _base.scss    .
// @import 'variables'; //     

@keyframes float {

    0%,
    100% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-8px);
    }
}

@keyframes pulse {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.05); //    
    }

    100% {
        transform: scale(1);
    }
}

@keyframes wave {
    0% {
        transform: translateX(0);
    }

    100% {
        transform: translateX(-50%);
    }
}

@keyframes slideInUp {
    from {
        opacity: 0;
        transform: translateY(50px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes fadeInScale {
    0% {
        opacity: 0;
        transform: scale(0.9);
    }

    100% {
        opacity: 1;
        transform: scale(1);
    }
}

@keyframes gradientAnimation {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes slideInRight {
    from {
        opacity: 0;
        transform: translateX(30px);
    }

    to {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes slideInLeft {
    from {
        opacity: 0;
        transform: translateX(-30px);
    }

    to {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes goldShine {
    0% {
        filter: drop-shadow(0 0 0 $primary-color-sass); //   Sass variable
    }

    50% {
        filter: drop-shadow(0 0 8px $primary-color-sass); //   Sass variable
    }

    100% {
        filter: drop-shadow(0 0 0 $primary-color-sass); //   Sass variable
    }
}

@keyframes shimmer {
    0% {
        transform: translateX(-100%);
    }

    100% {
        transform: translateX(100%);
    }
}

@keyframes border-glow {
    0% {
        box-shadow: 0 0 0 0 rgba($primary-color-sass, 0.4); //   Sass variable
    }

    50% {
        box-shadow: 0 0 15px 5px rgba($primary-color-sass, 0.7); //   Sass variable
    }

    100% {
        box-shadow: 0 0 0 0 rgba($primary-color-sass, 0.4); //   Sass variable
    }
}

@keyframes fadeInAndSlideUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes expandLine {
    from {
        width: 0;
    }

    to {
        width: 100%;
    }
}

@keyframes popIn {
    from {
        opacity: 0;
        transform: scale(0.8);
    }

    to {
        opacity: 1;
        transform: scale(1);
    }
}

@keyframes rotateAndScale {
    from {
        transform: rotate(0deg) scale(1);
    }

    to {
        transform: rotate(360deg) scale(1.1);
    }
}

@keyframes glowPulse {
    0% {
        filter: drop-shadow(0 0 5px rgba($primary-color-sass, 0.5)); //   Sass variable
    }

    50% {
        filter: drop-shadow(0 0 15px rgba($primary-color-sass, 0.9)); //   Sass variable
    }

    100% {
        filter: drop-shadow(0 0 5px rgba($primary-color-sass, 0.5)); //   Sass variable
    }
}

@keyframes pageFadeInUp {
    from {
        opacity: 0;
        transform: translateY(30px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes titleSlideIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes underlineGrow {
    from {
        width: 0;
    }

    to {
        width: 80px; // Adjust as needed
    }
}

@keyframes sectionZoomIn {
    from {
        opacity: 0;
        transform: scale(0.95);
    }

    to {
        opacity: 1;
        transform: scale(1);
    }
}

@keyframes inputBorderGlow {
    0% {
        box-shadow: 0 0 0 6px rgba($primary-color-sass, 0.3); //   Sass variable
    }

    50% {
        box-shadow: 0 0 0 8px rgba($primary-color-sass, 0.5); //   Sass variable
    }

    100% {
        box-shadow: 0 0 0 6px rgba($primary-color-sass, 0.3); //   Sass variable
    }
}

@keyframes buttonPressEffect {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(0.98);
    }

    100% {
        transform: scale(1);
    }
}