// src/scss/termsAndConditions.scss (      )

// Color Variables
$primary-color: #D4AF37; //   
$secondary-color: #B38D46; //   
$dark-bg: #121212; //  
$card-bg: #1E1E1E; //  
$light-text: #F5F5F5; //   
$border-color: rgba(212, 175, 55, 0.4); //     ( )
$accent-dark: #2A2A2A; //     
$accent-light: rgba(212, 175, 55, 0.15); //     ( )
$text-subtle: rgba($light-text, 0.75); //    ( )
$hover-highlight: rgba($primary-color, 0.2); //    

// Global Direction Variables (for RTL compatibility)
$dir-left: left;
$dir-right: right;

// ===============================================
//   
// ===============================================

//       
@keyframes pageFadeInSlideUp {
  from {
    opacity: 0;
    transform: translateY(40px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

//         
@keyframes cardPopInDeepShadow {
  0% {
    opacity: 0;
    transform: scale(0.9);
    box-shadow: 0 0 0 rgba(0, 0, 0, 0);
  }

  70% {
    opacity: 1;
    transform: scale(1.01);
    /*     */
    box-shadow: 0 25px 60px rgba(0, 0, 0, 0.9);
    /*    */
  }

  100% {
    opacity: 1;
    transform: scale(1);
    box-shadow: 0 18px 45px rgba(0, 0, 0, 0.85);
  }
}

//        
@keyframes headerGlowLinePulse {
  0% {
    background-position: -200% 0;
    opacity: 0.6;
  }

  50% {
    opacity: 1;
  }

  100% {
    background-position: 200% 0;
    opacity: 0.6;
  }
}

//     ()     
@keyframes listItemSlideInFade {
  from {
    opacity: 0;
    transform: translateX(-60px);
    /*   */
    filter: blur(5px);
    /*     */
  }

  to {
    opacity: 1;
    transform: translateX(0);
    filter: blur(0);
  }
}

//      
@keyframes numberPulseGlow {
  0% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba($primary-color, 0.6);
  }

  50% {
    transform: scale(1.1);
    box-shadow: 0 0 0 10px rgba($primary-color, 0);
    /*     */
  }

  100% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba($primary-color, 0);
  }
}

//      
@keyframes cardEdgeLight {
  0% {
    box-shadow: inset 0 0 5px rgba($primary-color, 0);
  }

  50% {
    box-shadow: inset 0 0 15px rgba($primary-color, 0.2);
  }

  100% {
    box-shadow: inset 0 0 5px rgba($primary-color, 0);
  }
}


// ===============================================
//   
// ===============================================
.termsAndConditions-page-container {
  min-height: 100vh;
  background-color: $dark-bg;
  color: $light-text;
  padding: 100px 20px 80px;
  /*     */
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Tajawal', sans-serif;
  animation: pageFadeInSlideUp 1s ease-out forwards;
  /*     */
  overflow-x: hidden;
  /*         */

  .termsAndConditions {
    max-width: 950px;
    /*     */
    width: 100%;
    background-color: $card-bg;
    border-radius: 20px;
    /*    */
    padding: 60px;
    /*    */
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.8);
    /*   */
    border: 2px solid $border-color;
    /*    */
    position: relative;
    overflow: hidden;
    /*      */
    animation: cardPopInDeepShadow 1s ease-out 0.3s forwards;
    /*    */
    opacity: 0;
    /*   */
    transform: scale(0.9);
    /*    */

    &::before {
      /*    */
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 6px;
      /*   */
      background: linear-gradient(90deg, transparent, $primary-color, transparent);
      background-size: 200% 100%;
      /*   */
      animation: headerGlowLinePulse 4s linear infinite;
      /*   */
      filter: drop-shadow(0 0 10px rgba($primary-color, 0.8));
      /*   */
    }

    .terms-header {
      margin-bottom: 50px;
      /*    */
      text-align: center;
      padding-bottom: 30px;
      /*    */
      border-bottom: 1px solid rgba($border-color, 0.5);
      /*   */
      position: relative;
      /*    */

      &::after {
        /*      */
        content: '';
        position: absolute;
        bottom: -2px;
        /*     */
        left: 50%;
        transform: translateX(-50%);
        width: 100px;
        height: 3px;
        background: linear-gradient(90deg, $primary-color, $secondary-color);
        border-radius: 2px;
      }

      .terms-title {
        /*   */
        color: $primary-color;
        font-size: 3.8rem;
        /*     */
        margin-bottom: 25px;
        /*   */
        font-weight: 900;
        /*   */
        text-shadow: 0 6px 12px rgba(0, 0, 0, 0.8);
        /*    */
        letter-spacing: 2px;
        /*    */
        line-height: 1.1;
        /*    */
      }

      .intro-text {
        /*   */
        font-size: 1.3rem;
        /*    */
        line-height: 1.8;
        color: $text-subtle;
        /*    */
        max-width: 700px;
        /*     */
        margin: 0 auto;
        /*   */
      }
    }

    .terms-list {
      padding: 0;
      counter-reset: term-counter;
      /*    */

      .terms-list-item {
        padding: 30px;
        /*   */
        margin-bottom: 25px;
        /*     */
        background-color: $accent-dark;
        /*    */
        border-radius: 12px;
        /*    */
        border-left: 5px solid $primary-color;
        /*    */
        position: relative;
        overflow: hidden;
        /*      */
        transition: all 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94);
        /*    */
        display: flex;
        align-items: flex-start;
        font-size: 1.15rem;
        /*     */
        line-height: 1.8;
        box-shadow: 0 8px 25px rgba(0, 0, 0, 0.4);
        /*   */
        color: $light-text;
        /*     */

        /*       */
        &:not(:first-child) {
          animation: listItemSlideInFade 0.8s ease-out forwards;
          opacity: 0;
          transform: translateX(-60px);
        }

        /*     */
        @for $i from 1 through 10 {

          /*        */
          &:nth-child(#{$i}) {
            animation-delay: 0.5s + ($i - 1) * 0.1s;
          }
        }

        &:hover {
          background-color: $hover-highlight;
          /*       */
          transform: translateX(15px);
          /*     */
          box-shadow: 0 12px 30px rgba(0, 0, 0, 0.6);
          border-left-color: lighten($primary-color, 10%);
          /*     */
        }

        .term-number {
          /*    */
          display: inline-flex;
          align-items: center;
          justify-content: center;
          width: 45px;
          /*    */
          height: 45px;
          background-color: $primary-color;
          color: $dark-bg;
          border-radius: 50%;
          font-weight: 900;
          /*    */
          font-size: 1.4rem;
          /*     */
          margin-inline-end: 25px;
          /*   */
          flex-shrink: 0;
          /*    */
          animation: numberPulseGlow 3s infinite ease-in-out;
          /*     */
          box-shadow: 0 0 0 0 rgba($primary-color, 0.6);
          /*   */
          text-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
          /*    */
        }
      }
    }
  }
}

// ===============================================
//  RTL (Right-to-Left)
// ===============================================
[dir="rtl"] {
  .termsAndConditions-page-container {
    .termsAndConditions {
      .terms-header {
        /*    text-align: center; */
      }

      .terms-list {
        .terms-list-item {
          border-left: none;
          /*     */
          border-right: 5px solid $primary-color;
          /*    */

          &:hover {
            transform: translateX(-15px);
            /*    */
          }

          .term-number {
            margin-inline-end: 0;
            /*     */
            margin-inline-start: 25px;
            /*     */
          }
        }
      }
    }
  }
}

// ===============================================
//   (Responsive Design)
// ===============================================
@media (max-width: 992px) {

  /* Tablets and smaller laptops */
  .termsAndConditions-page-container {
    padding: 80px 15px 50px;

    .termsAndConditions {
      padding: 50px;

      .terms-header {
        .terms-title {
          font-size: 3.2rem;
        }

        .intro-text {
          font-size: 1.2rem;
        }
      }

      .terms-list {
        .terms-list-item {
          padding: 25px;
          font-size: 1.05rem;

          .term-number {
            width: 40px;
            height: 40px;
            font-size: 1.3rem;
            margin-inline-end: 20px;
          }
        }
      }
    }
  }

  [dir="rtl"] {
    .termsAndConditions-page-container {
      .termsAndConditions {
        .terms-list {
          .terms-list-item {
            .term-number {
              margin-inline-start: 20px;
            }
          }
        }
      }
    }
  }
}

@media (max-width: 768px) {

  /* Smaller tablets and large phones */
  .termsAndConditions-page-container {
    padding: 60px 10px 40px;

    .termsAndConditions {
      padding: 35px;
      border-radius: 15px;

      .terms-header {
        margin-bottom: 35px;

        .terms-title {
          font-size: 2.6rem;
          margin-bottom: 20px;
        }

        .intro-text {
          font-size: 1.1rem;
        }
      }

      .terms-list {
        .terms-list-item {
          padding: 20px;
          font-size: 1rem;
          flex-direction: column;
          /*         */
          text-align: center;
          /*   */
          align-items: center;
          /*     */

          .term-number {
            margin-bottom: 15px;
            /*    */
            margin-inline-end: 0;
            /*      */
            width: 38px;
            height: 38px;
            font-size: 1.2rem;
          }
        }
      }
    }
  }

  [dir="rtl"] {
    .termsAndConditions-page-container {
      .termsAndConditions {
        .terms-list {
          .terms-list-item {
            border-right: 5px solid $primary-color;
            /*     */
            border-left: none;
            /*     */
            transform: none !important;

            /*       */
            &:hover {
              transform: none !important;
              /*      */
            }

            .term-number {
              margin-inline-start: 0;
              /*      */
            }
          }
        }
      }
    }
  }
}

@media (max-width: 480px) {

  /* Small phones */
  .termsAndConditions-page-container {
    padding: 40px 8px 25px;

    .termsAndConditions {
      padding: 25px;
      border-radius: 12px;

      .terms-header {
        margin-bottom: 25px;

        .terms-title {
          font-size: 2rem;
          margin-bottom: 15px;
        }

        .intro-text {
          font-size: 0.9rem;
        }
      }

      .terms-list {
        .terms-list-item {
          padding: 15px;
          font-size: 0.85rem;
          margin-bottom: 15px;
          border-left-width: 3px;
          /*   */
          border-right-width: 3px;
          /*   */

          .term-number {
            width: 32px;
            height: 32px;
            font-size: 1.1rem;
            margin-bottom: 10px;
          }
        }
      }
    }
  }
}