// src/scss/cancelDialoge.scss -   

// Color Variables
$primary-color: #D4AF37; //   
$secondary-color: #B38D46; //   
$dark-bg: #121212; //  
$card-bg: #1E1E1E; //  
$light-text: #F5F5F5; //   
$border-color: rgba(212, 175, 55, 0.3); //   
$error-red: #ff4d4f; //   
$button-hover-bg: rgba(212, 175, 55, 0.1); //     

//  RGB   (  rgba())
$primary-rgb: rgb(212, 175, 55);
$dark-bg-rgb: rgb(18, 18, 18);
$error-red-rgb: rgb(255, 77, 79);

// Animation Variables
@keyframes fadeInScale {
    from {
        opacity: 0;
        transform: scale(0.9);
    }

    to {
        opacity: 1;
        transform: scale(1);
    }
}

@keyframes slideInFromTop {
    from {
        opacity: 0;
        transform: translateY(-20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes buttonPulse {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.03);
    }

    100% {
        transform: scale(1);
    }
}


.cancel-dialoge-root {

    //    Modal Backdrop     JSX
    .MuiDialog-container {
        //  Dialog   
        justify-content: center;
        align-items: center;
    }

    .cancel-dialoge-paper {
        //  Paper     
        background: linear-gradient(135deg, $card-bg 0%, rgba($dark-bg-rgb, 0.9) 100%); //  
        border: 1px solid rgba($border-color, 0.7); //  
        box-shadow: 0 10px 40px rgba(0, 0, 0, 0.8); //  
        border-radius: 16px !important; //   
        max-width: 480px; //    ( )
        width: 90%; //  
        outline: none; //  outline 
        animation: fadeInScale 0.3s ease-out forwards; //   
        font-family: 'Tajawal', sans-serif;
        color: $light-text; //   

        .dialog-title {
            color: $primary-color;
            font-size: 1.8rem !important; //   
            font-weight: 700 !important;
            text-align: center !important;
            padding: 25px 30px 15px !important; //  
            border-bottom: 1px solid rgba($border-color, 0.3); //  
            position: relative;
            text-shadow: 0 0 8px rgba($primary-color, 0.4); //  

            &::after {
                //    
                content: '';
                position: absolute;
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
                width: 60px;
                height: 2px;
                background: linear-gradient(90deg, transparent, $primary-color, transparent);
            }
        }

        .dialog-content {
            padding: 25px 30px !important; //  
            text-align: center;

            .dialog-text {
                color: rgba($light-text, 0.8);
                font-size: 1rem;
                line-height: 1.6;
                margin: 0;
            }
        }

        .dialog-actions {
            padding: 15px 25px !important; //  
            justify-content: space-around !important; //    
            border-top: 1px solid rgba($border-color, 0.2); //   

            .MuiButton-root {
                //    MUI
                padding: 12px 25px; //  
                border-radius: 8px; //  
                font-size: 1rem;
                font-weight: 600;
                text-transform: none; //    
                transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
                box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);

                &:hover {
                    transform: translateY(-3px); //   
                    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.4);
                }

                &:disabled {
                    opacity: 0.6;
                    cursor: not-allowed;
                    transform: translateY(0);
                    box-shadow: none;
                }
            }

            .delete-btn {
                //  " " ()
                background-color: $error-red !important;
                color: $light-text !important;
                border: 1px solid rgba($error-red-rgb, 0.7);

                &:hover {
                    background-color: darken($error-red, 10%) !important;
                    box-shadow: 0 6px 18px rgba($error-red-rgb, 0.6) !important;
                }

                .MuiCircularProgress-root {
                    //  
                    color: $light-text;
                }
            }

            .back-btn {
                //  "" (/)
                background-color: transparent !important;
                color: $primary-color !important;
                border: 2px solid $primary-color !important; //   
                box-shadow: none;

                &:hover {
                    background-color: $button-hover-bg !important;
                    box-shadow: 0 4px 15px rgba($primary-rgb, 0.2) !important;
                }
            }
        }
    }
}


/* ============================================== */
/* *** RTL (Right-to-Left) Adjustments *** */
/* ============================================== */
[dir="rtl"] {
    .cancel-dialoge-paper {

        //      positioning  
        .dialog-title,
        .dialog-content {
            text-align: right !important; //  
        }

        .dialog-actions {
            flex-direction: row-reverse; //   

            .delete-btn {
                //    RTL
                margin-left: 0;
                margin-right: 15px; //   
            }
        }
    }
}


/* ============================================== */
/* *** Responsive Styles *** */
/* ============================================== */
@media (max-width: 576px) {
    .cancel-dialoge-paper {
        max-width: 95%; //     
        padding: 20px !important;

        .dialog-title {
            font-size: 1.5rem !important;
            padding: 20px 15px 10px !important;
        }

        .dialog-content {
            padding: 20px 15px !important;

            .dialog-text {
                font-size: 0.9rem;
            }
        }

        .dialog-actions {
            padding: 10px 15px !important;
            flex-direction: column-reverse; //   
            gap: 10px; //   

            .MuiButton-root {
                width: 100%; //   
                font-size: 0.9rem;
                padding: 10px 15px;
            }
        }
    }
}