//   (   )
$primary-color: #D4AF37; //   
$secondary-color: #B38D46; //   
$dark-bg: #121212; //  
$card-bg: #1E1E1E; //  
$light-text: #F5F5F5; //   
$border-color: rgba(212, 175, 55, 0.4); //    ( )
$accent-dark: #2A2A2A; //     
$accent-light: rgba(212, 175, 55, 0.15); //     ( )
$text-subtle: rgba($light-text, 0.75); //    ( )
$glow-color: rgba($primary-color, 0.8); //   

// Global Direction Variables (for RTL compatibility)
$dir-left: left;
$dir-right: right;


// ===============================================
//   
// ===============================================

//          
@keyframes pageFadeInSlideUpBlur {
    from {
        opacity: 0;
        transform: translateY(50px);
        filter: blur(8px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
        filter: blur(0);
    }
}

//         
@keyframes sectionTitleReveal {
    0% {
        opacity: 0;
        transform: translateY(30px);
        text-shadow: 0 0 0 rgba(0, 0, 0, 0);
    }

    50% {
        opacity: 1;
        transform: translateY(0);
        text-shadow: 0 0 15px rgba($glow-color, 0.6);
    }

    100% {
        opacity: 1;
        transform: translateY(0);
        text-shadow: 0 0 8px rgba(0, 0, 0, 0.5);
    }
}

@keyframes expandLineGlow {
    0% {
        width: 0;
        opacity: 0;
        transform: translateX(-50%);
        /*     */
        background-size: 0% 100%;
    }

    50% {
        width: 100px;
        opacity: 1;
        transform: translateX(-50%);
        background-size: 100% 100%;
        box-shadow: 0 0 15px rgba($glow-color, 0.7);
    }

    100% {
        width: 80px;
        /*   */
        opacity: 1;
        transform: translateX(-50%);
        background-size: 100% 100%;
        box-shadow: 0 0 5px rgba($glow-color, 0.3);
    }
}

//        
@keyframes cardEntrance {
    from {
        opacity: 0;
        transform: translateY(40px) scale(0.95);
        box-shadow: 0 0 0 rgba(0, 0, 0, 0);
        filter: brightness(0.5);
    }

    to {
        opacity: 1;
        transform: translateY(0) scale(1);
        box-shadow: 0 10px 30px rgba(0, 0, 0, 0.6);
        filter: brightness(1);
    }
}

//       
@keyframes pulseSubtle {
    0% {
        transform: scale(1);
        opacity: 1;
    }

    50% {
        transform: scale(1.02);
        opacity: 0.95;
    }

    100% {
        transform: scale(1);
        opacity: 1;
    }
}

// ===============================================
//   
// ===============================================
.chalet-page-container {
    min-height: 100vh;
    padding: 100px 20px 60px;
    /*     */
    background-color: $dark-bg;
    color: $light-text;
    font-family: 'Tajawal', 'Roboto', sans-serif;
    animation: pageFadeInSlideUpBlur 1.2s ease-out forwards;
    /*     */
    overflow-x: hidden;
    /*         */
    display: flex;
    flex-direction: column;
    align-items: center;
    /*   */

    .section-title {
        margin: 0 auto 80px;
        /*    */
        font-size: 3.5rem;
        /*     */
        color: $primary-color;
        text-align: center;
        font-weight: 900;
        /*   */
        position: relative;
        padding-bottom: 20px;
        /*     */
        text-shadow: 0 4px 10px rgba(0, 0, 0, 0.7);
        /*    */
        letter-spacing: 2px;
        /*   */
        opacity: 0;
        /*   */
        animation: sectionTitleReveal 1.2s ease-out forwards 0.5s;
        /*    */

        &::after {
            content: '';
            position: absolute;
            left: 50%;
            bottom: 0;
            transform: translateX(-50%);
            width: 0;
            /*   0   */
            height: 5px;
            /*   */
            background: linear-gradient(90deg, $primary-color, $secondary-color);
            border-radius: 5px;
            animation: expandLineGlow 1.5s ease-out forwards 1.2s;
            /*      */
            box-shadow: 0 0 10px rgba($glow-color, 0.5);
            /*   */
        }
    }

    .chalet-grid {
        /*   halls-grid     */
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(380px, 1fr));
        /*    */
        gap: 50px;
        /*     */
        justify-content: center;
        padding: 20px 0;
        width: 100%;
        /*       */
        max-width: 1300px;
        /*     */

        /*     */
        .MuiGrid-item {
            /*    Grid item    Card */
            animation: cardEntrance 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
            opacity: 0;
            /*   */
            transform: translateY(40px) scale(0.95);

            @for $i from 1 through 9 {

                /*    9  */
                &:nth-child(#{$i}) {
                    animation-delay: 0.8s + ($i - 1) * 0.15s;
                    /*     */
                }
            }
        }
    }

    /*    (   ) */
    .loading-message,
    .no-results-message {
        text-align: center;
        color: $text-subtle;
        margin-top: 50px;
        font-size: 1.8rem;
        /*   */
        font-weight: 600;
        animation: fadeInUp 0.8s ease-out;
        /*   fadeInUp  */
        padding: 30px;
        /*   */
        background-color: rgba($primary-color, 0.08);
        /*    */
        border-radius: 12px;
        border: 2px solid $border-color;
        /*   */
        box-shadow: 0 8px 20px rgba(0, 0, 0, 0.4);
    }

    /*      */
    .pagination-controls {
        /*   .pagination-container  pagination-controls   Halls.jsx */
        display: flex;
        justify-content: center;
        margin-top: 70px;
        /*    */
        padding: 25px 0;
        animation: fadeInUp 0.8s ease-out 1s both;
        /*    */

        .MuiPaginationItem-root {
            color: $light-text;
            background-color: $card-bg;
            border: 2px solid $border-color;
            /*    */
            transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
            /*   */
            margin: 0 8px;
            /*     */
            font-weight: 600;
            /*   */
            font-size: 1.1rem;
            /*   */
            min-width: 45px;
            /*    */
            height: 45px;
            /*   */
            border-radius: 10px;
            /*    */
            font-family: 'Tajawal', 'Roboto', sans-serif;

            &:hover {
                background-color: rgba($primary-color, 0.25);
                /*     */
                transform: translateY(-5px) scale(1.05);
                /*      */
                box-shadow: 0 8px 20px rgba(0, 0, 0, 0.4);
                border-color: $primary-color;
                /*   */
            }

            &.Mui-selected {
                background-color: $primary-color;
                color: $dark-bg;
                font-weight: bold;
                box-shadow: 0 0 15px rgba($primary-color, 0.7);
                /*     */
                animation: glow 1.5s infinite alternate;
                /*     */
                transform: translateY(-2px);
                /*      */

                &:hover {
                    background-color: darken($primary-color, 8%);
                    /*       */
                    transform: translateY(-7px) scale(1.08);
                    /*     */
                }
            }
        }
    }
}

// ===============================================
//  RTL
// ===============================================
[dir="rtl"] {
    .chalet-page-container {
        .section-title::after {
            left: auto;
            right: 50%;
            transform: translateX(50%);
        }

        .status-message {
            border-left: none;
            border-right: 2px solid $primary-color;
            /*     RTL */
        }

        .pagination-controls {

            /*   .pagination-container  pagination-controls   Halls.jsx */
            .MuiPagination-ul {
                flex-direction: row-reverse;
                /*    */
            }
        }
    }
}

// ===============================================
//   (Responsive Design)
// ===============================================
@media (max-width: 1200px) {

    /* Tablets and smaller laptops */
    .chalet-page-container {
        padding: 80px 15px 50px;

        .section-title {
            font-size: 3rem;
            margin-bottom: 60px;
        }

        .chalet-grid {
            grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
            /*    */
            gap: 40px;
        }
    }
}

@media (max-width: 992px) {

    /* Smaller tablets and large phones */
    .chalet-page-container {
        padding: 60px 10px 40px;

        .section-title {
            font-size: 2.5rem;
            margin-bottom: 50px;
        }

        .chalet-grid {
            grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
            /*     */
            gap: 30px;
        }
    }
}

@media (max-width: 768px) {

    /* Medium phones */
    .chalet-page-container {
        padding: 50px 10px 30px;

        .section-title {
            font-size: 2rem;
            margin-bottom: 40px;
            letter-spacing: 1.5px;

            &::after {
                width: 60px;
                height: 4px;
            }
        }

        .chalet-grid {
            grid-template-columns: 1fr;
            /*   */
            gap: 25px;
            max-width: 400px;
            /*        */
        }

        .loading-message,
        .no-results-message {
            font-size: 1.5rem;
            padding: 25px;
        }

        .pagination-controls {
            margin-top: 40px;

            .MuiPaginationItem-root {
                min-width: 40px;
                height: 40px;
                font-size: 1rem;
                margin: 0 4px;
            }
        }
    }
}

@media (max-width: 480px) {

    /* Small phones */
    .chalet-page-container {
        padding: 40px 8px 20px;

        .section-title {
            font-size: 1.8rem;
            margin-bottom: 30px;

            &::after {
                height: 3px;
            }
        }

        .loading-message,
        .no-results-message {
            font-size: 1.3rem;
            padding: 20px;
        }

        .pagination-controls {
            margin-top: 30px;

            .MuiPaginationItem-root {
                min-width: 35px;
                height: 35px;
                font-size: 0.9rem;
            }
        }
    }
}