//   (   )
$primary-color: #D4AF37;
$secondary-color: #B38D46;
$dark-bg: #121212;
$card-bg: #1E1E1E;
$light-text: #F5F5F5;
$border-color: rgba(212, 175, 55, 0.3);
$overlay-dark: rgba(0, 0, 0, 0.6); //  
$control-bg: rgba(30, 30, 30, 0.7); //   

// 
@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes slideInFromBottom {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes imageFadeIn {
  from {
    opacity: 0.7;
    transform: scale(0.98);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

.image-gallery-container {
  width: 100%;
  max-width: 1000px; //   
  margin: 40px auto; //   
  background-color: $card-bg;
  border-radius: 16px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.7);
  border: 1px solid rgba($border-color, 0.6);
  overflow: hidden; //      
  font-family: 'Tajawal', sans-serif;
  position: relative; //   
  animation: fadeIn 0.8s ease-out; //    

  .main-image-wrapper {
    position: relative; //    
    width: 100%;
    padding-bottom: 56.25%; //     16:9    ( )
    overflow: hidden;
    background-color: $dark-bg; //  

    .main-image {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover; //      
      display: block;
      animation: imageFadeIn 0.6s ease-out; //    
    }

    .carousel-control {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      background-color: $control-bg; //   
      color: $primary-color;
      border: none;
      border-radius: 50%; // 
      width: 50px;
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      font-size: 1.8rem;
      opacity: 0.8;
      transition: all 0.3s ease;
      z-index: 10; //    

      &:hover {
        opacity: 1;
        background-color: rgba($primary-color, 0.2);
        box-shadow: 0 0 15px rgba($primary-color, 0.5);
        transform: translateY(-50%) scale(1.1); //  
      }

      &.prev {
        left: 15px; //   
        // For RTL, this will be handled by the [dir="rtl"] block
      }

      &.next {
        right: 15px; //   
        // For RTL, this will be handled by the [dir="rtl"] block
      }
    }
  }

  .thumbnail-images-wrapper {
    display: flex;
    justify-content: center;
    flex-wrap: wrap; //     
    gap: 15px; //    
    padding: 20px; //  
    background-color: $dark-bg; //    
    border-top: 1px solid rgba($border-color, 0.4);
    border-bottom-left-radius: 16px;
    border-bottom-right-radius: 16px;
    animation: slideInFromBottom 0.8s ease-out 0.6s forwards; //  
    opacity: 0; //  
    transform: translateY(20px); //   

    .thumbnail-image {
      width: 100px; //   
      height: 70px;
      object-fit: cover;
      border-radius: 8px;
      border: 2px solid transparent; //   
      cursor: pointer;
      transition: all 0.2s ease-in-out;
      opacity: 0.7;
      box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);

      &:hover {
        opacity: 1;
        border-color: rgba($primary-color, 0.7);
        transform: scale(1.05); //    
      }

      &.active {
        border-color: $primary-color; //    
        opacity: 1;
        box-shadow: 0 0 15px rgba($primary-color, 0.6); //  
        transform: scale(1.02); //  
      }
    }
  }
}

//  RTL
[dir="rtl"] {
  .image-gallery-container {
    .main-image-wrapper {
      .carousel-control {
        &.prev {
          left: auto;
          right: 15px; //  
          transform: translateY(-50%) scaleX(-1); //   

          &:hover {
            transform: translateY(-50%) scale(1.1) scaleX(-1);
          }
        }

        &.next {
          right: auto;
          left: 15px; //  
          transform: translateY(-50%) scaleX(-1); //   

          &:hover {
            transform: translateY(-50%) scale(1.1) scaleX(-1);
          }
        }
      }
    }
  }
}

//   (     )
@media (max-width: 768px) {
  .image-gallery-container {
    margin: 20px auto;
    border-radius: 12px;
    padding: 0; //     

    .main-image-wrapper {
      padding-bottom: 65%; //       
      border-radius: 12px 12px 0 0; //   

      .carousel-control {
        width: 40px;
        height: 40px;
        font-size: 1.5rem;

        &.prev {
          left: 10px;
        }

        &.next {
          right: 10px;
        }
      }
    }

    .thumbnail-images-wrapper {
      padding: 15px;
      gap: 10px;
      border-top: 1px solid rgba($border-color, 0.3);
      border-radius: 0 0 12px 12px; //   

      .thumbnail-image {
        width: 80px;
        height: 60px;
        border-radius: 6px;
      }
    }
  }
}

@media (max-width: 480px) {
  .image-gallery-container {
    margin: 15px auto;
    border-radius: 8px;

    .main-image-wrapper {
      padding-bottom: 75%; //     
      border-radius: 8px 8px 0 0;

      .carousel-control {
        width: 35px;
        height: 35px;
        font-size: 1.2rem;
      }
    }

    .thumbnail-images-wrapper {
      padding: 10px;
      gap: 8px;
      border-radius: 0 0 8px 8px;

      .thumbnail-image {
        width: 60px;
        height: 45px;
        border-radius: 5px;
      }
    }
  }
}