//   (   )
$primary-color: #D4AF37; //   
$secondary-color: #B38D46; //   
$dark-bg: #121212; //   
$card-bg: #1E1E1E; //   ( )
$light-text: #F5F5F5; //   
$border-color: rgba(212, 175, 55, 0.4); //    ( )
$error-color: #ff4d4f; //   
$glow-color: rgba($primary-color, 0.8); //   
$input-bg-dark: lighten($card-bg, 5%); //   


// Global Direction Variables (for RTL compatibility)
$dir-left: left;
$dir-right: right;


// ===============================================
//   
// ===============================================

//         
@keyframes pageFadeInSlideUpBlur {
  from {
    opacity: 0;
    transform: translateY(50px);
    filter: blur(8px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
    filter: blur(0);
  }
}

//     UI (  )   
@keyframes elementFadeInUp {
  from {
    opacity: 0;
    transform: translateY(40px);
    filter: blur(4px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
    filter: blur(0);
  }
}

//      
@keyframes borderShimmerPulse {
  0% {
    background-position: -200% 0;
    opacity: 0.7;
  }

  50% {
    opacity: 1;
  }

  100% {
    background-position: 200% 0;
    opacity: 0.7;
  }
}

//      (    )
@keyframes continuousGlow {
  0% {
    box-shadow: 0 0 8px rgba($primary-color, 0.4);
  }

  50% {
    box-shadow: 0 0 25px rgba($primary-color, 0.9);
    /*   */
  }

  100% {
    box-shadow: 0 0 8px rgba($primary-color, 0.4);
  }
}

//     
@keyframes subtleFloat {

  0%,
  100% {
    transform: translate(0, 0) scale(1);
  }

  25% {
    transform: translate(10px, -10px) scale(1.02);
  }

  50% {
    transform: translate(0, 15px) scale(1.05);
  }

  75% {
    transform: translate(-10px, 5px) scale(1.03);
  }
}

//      
@keyframes logoHoverEffect {
  from {
    transform: rotate(0deg) scale(1);
    filter: drop-shadow(0 0 10px rgba($primary-color, 0.5));
  }

  to {
    transform: rotate(15deg) scale(1.1);
    filter: drop-shadow(0 0 20px rgba($primary-color, 0.9));
  }
}

//     
@keyframes expandLineSmooth {
  from {
    width: 0;
    opacity: 0;
  }

  to {
    width: 60px;
    /*   */
    opacity: 1;
  }
}

//   
@keyframes buttonPulse {
  0% {
    transform: translateY(0);
    box-shadow: 0 5px 15px rgba($primary-color, 0.4);
  }

  50% {
    transform: translateY(-5px);
    box-shadow: 0 10px 25px rgba($primary-color, 0.7);
  }

  100% {
    transform: translateY(0);
    box-shadow: 0 5px 15px rgba($primary-color, 0.4);
  }
}


// ===============================================
//   
// ===============================================
.auth-page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: $dark-bg;
  position: relative;
  overflow: hidden;
  /*    scrollbars    */
  font-family: 'Tajawal', 'Roboto', sans-serif;
  color: $light-text;
  animation: pageFadeInSlideUpBlur 1.2s ease-out forwards;
  /*     */
}

.auth-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  padding: 60px 20px;
  /*   */
  position: relative;
  z-index: 2;
  /*    */
}

.auth-card {
  background: $card-bg;
  border: 2px solid $border-color;
  /*    */
  border-radius: 20px;
  /*    */
  box-shadow: 0 15px 50px rgba(0, 0, 0, 0.8);
  /*    */
  padding: 50px;
  /*   */
  width: 100%;
  max-width: 550px;
  /*    */
  animation: elementFadeInUp 1s ease-out 0.3s both;
  /*    */
  position: relative;
  overflow: hidden;

  &::before {
    /*    */
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 5px;
    /*   */
    background: linear-gradient(90deg, $primary-color, $secondary-color, $primary-color);
    background-size: 200% 100%;
    /*   */
    animation: borderShimmerPulse 3s linear infinite;
    /*    */
  }
}

.logo-container {
  text-align: center;
  margin-bottom: 40px;
  /*    */
  animation: elementFadeInUp 1s ease-out 0.2s both;
  /*    */

  .logo {
    height: 100px;
    /*    */
    width: 100px;
    object-fit: contain;
    margin-bottom: 15px;
    transition: all 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    /*   */
    filter: drop-shadow(0 0 12px rgba($primary-color, 0.6));
    /*    */

    &:hover {
      animation: logoHoverEffect 0.8s forwards;
      /*      */
    }
  }

  .title {
    color: $primary-color;
    font-size: 2.2rem;
    /*      */
    font-weight: 800;
    /*   */
    margin: 0;
    position: relative;
    text-shadow: 0 0 10px rgba($primary-color, 0.7);
    /*    */
    letter-spacing: 1px;
    /*   */

    &::after {
      /*     */
      content: '';
      position: absolute;
      bottom: -15px;
      /*   */
      left: 50%;
      transform: translateX(-50%);
      width: 0;
      /*   0 */
      height: 4px;
      /*   */
      background: $primary-color;
      border-radius: 3px;
      animation: expandLineSmooth 1s ease-out forwards 0.8s;
      /*    */
    }
  }
}

.form-title {
  color: $primary-color;
  font-size: 1.8rem;
  /*      */
  text-align: center;
  margin-bottom: 30px;
  /*    */
  font-weight: 700;
  /*   */
  position: relative;
  text-shadow: 0 0 8px rgba($primary-color, 0.5);
  /*    */
  animation: elementFadeInUp 1s ease-out 0.4s both;
  /*     */

  &::after {
    /*      */
    content: '';
    position: absolute;
    bottom: -12px;
    left: 50%;
    transform: translateX(-50%);
    width: 0;
    /*   0 */
    height: 3px;
    background: $primary-color;
    border-radius: 3px;
    animation: expandLineSmooth 0.8s ease-out forwards 0.6s;
    /*    */
  }
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 25px;
  /*      */
  animation: elementFadeInUp 1s ease-out 0.6s both;
  /*    */
}

.phone-input-wrapper {
  direction: ltr;
  /*  */
  margin-bottom: 25px;
  /*   */
  animation: elementFadeInUp 1s ease-out 0.5s both;
  /*     */

  .react-tel-input {
    .form-control {
      height: 60px;
      /*     */
      width: 100%;
      background-color: $input-bg-dark;
      /*    */
      color: $light-text;
      border: 2px solid $border-color;
      /*   */
      border-radius: 10px;
      /*    */
      padding-left: 65px;
      /*   */
      font-size: 1.1rem;
      /*   */
      transition: all 0.4s ease;
      box-shadow: 0 3px 12px rgba(0, 0, 0, 0.5);
      /*   */

      &::placeholder {
        color: rgba($light-text, 0.5);
        /*  placeholder  */
      }
    }

    .flag-dropdown {
      background-color: $input-bg-dark;
      /*    */
      border: 2px solid $border-color;
      /*   */
      border-right: none;
      border-radius: 10px 0 0 10px;
      /*    */
      transition: all 0.4s ease;

      &:hover {
        background-color: rgba($primary-color, 0.15);
        /*     */
      }

      .selected-flag {
        &:hover {
          background-color: transparent;
        }

        .arrow {
          border-top-color: $primary-color;

          /*    */
          &.up {
            border-bottom-color: $primary-color;
          }
        }
      }
    }

    .country-list {
      background-color: $card-bg;
      border: 1px solid $border-color;
      border-radius: 10px;
      box-shadow: 0 8px 20px rgba(0, 0, 0, 0.6);
      /*   */

      .country {
        &:hover {
          background-color: rgba($primary-color, 0.15);
        }

        &.highlight {
          background-color: rgba($primary-color, 0.25);
          /*    */
        }

        .dial-code {
          color: $primary-color;
          font-weight: 700;
          /*   */
        }
      }

      .search-box {
        background-color: $dark-bg;
        border: 1px solid rgba($primary-color, 0.5);
        color: $light-text;
        margin: 12px;
        padding: 10px 12px;
        border-radius: 8px;

        &::placeholder {
          color: rgba($light-text, 0.5);
        }
      }
    }
  }

  /* Styling when the PhoneInput is focused */
  &.focused {
    .react-tel-input {
      .form-control {
        border-color: $primary-color;
        box-shadow: 0 0 0 4px rgba($primary-color, 0.4);
        /*     */
        outline: none;
      }

      .flag-dropdown {
        border-color: $primary-color;
        box-shadow: 0 0 0 4px rgba($primary-color, 0.4);
      }
    }
  }

  .error-message-phone {
    color: $error-color;
    font-size: 0.85rem;
    /*     */
    margin-top: 5px;
    /*    */
    margin-left: 10px;
    /*   */
    direction: ltr;
    /*     */
    animation: fadeIn 0.3s ease-out;
    font-weight: 500;
  }
}

.auth-input {

  /*     ( TextField  MUI) */
  .MuiInputBase-root {
    background-color: $input-bg-dark !important;
    color: $light-text !important;
    border-radius: 10px !important;
    /*    */
    box-shadow: 0 3px 12px rgba(0, 0, 0, 0.5) !important;
    /*   */
    transition: all 0.4s ease;
    height: 60px;
    /*    */

    .MuiOutlinedInput-notchedOutline {
      border-color: $border-color !important;
      /*    */
      border-width: 2px !important;
      /*   */
    }

    &:hover .MuiOutlinedInput-notchedOutline {
      border-color: $primary-color !important;
      /*    */
    }

    &.Mui-focused .MuiOutlinedInput-notchedOutline {
      border-color: $primary-color !important;
      box-shadow: 0 0 0 4px rgba($primary-color, 0.4) !important;
      /*     */
    }

    input {
      /*     */
      padding: 18px 14px;
      /*   */
      font-size: 1.1rem;
      /*   */
    }
  }

  .MuiFormHelperText-root {
    color: $error-color !important;
    font-size: 0.85rem !important;
    /*     */
    margin-left: 14px !important;
    /*  */
    font-weight: 500;
  }
}

.auth-actions {
  display: flex;
  flex-direction: column;
  gap: 20px;
  /*     */
  margin-top: 40px;
  /*    */
}

.auth-button {
  border-radius: 35px !important;
  /*     */
  padding: 16px !important;
  /*    */
  font-weight: 800 !important;
  /*   */
  text-transform: none !important;
  font-size: 1.2rem !important;
  /*   */
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) !important;
  /*    */
  letter-spacing: 1px;
  /*   */

  &.primary {
    background-color: $primary-color !important;
    color: $dark-bg !important;
    box-shadow: 0 8px 20px rgba($primary-color, 0.5) !important;
    /*   */
    animation: buttonPulse 2s infinite alternate ease-in-out;
    /*     */

    &:hover {
      background-color: darken($primary-color, 8%) !important;
      /*    */
      transform: translateY(-5px);
      /*   */
      box-shadow: 0 12px 30px rgba($primary-color, 0.8) !important;
      /*    */
    }
  }

  &.secondary {
    background-color: transparent !important;
    border: 2px solid $primary-color !important;
    /*   */
    color: $primary-color !important;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.3) !important;
    /*   */

    &:hover {
      background-color: rgba($primary-color, 0.2) !important;
      /*     */
      transform: translateY(-3px);
      /*   */
      box-shadow: 0 5px 15px rgba(0, 0, 0, 0.4) !important;
    }
  }
}

.back-button {
  position: absolute;
  top: 30px;
  /*   */
  #{$dir-left}: 30px;
  /*   */
  background-color: $primary-color;
  color: $dark-bg;
  border: none;
  border-radius: 50%;
  width: 55px;
  /*   */
  height: 55px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.4);
  /*   */
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  /*   */
  z-index: 10;
  animation: elementFadeInUp 0.8s ease-out 0.1s both;
  /*     */
  font-size: 1.8rem;
  /*    */

  &:hover {
    transform: scale(1.2) rotate(-15deg);
    /*      */
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.5);
  }
}

.auth-decoration {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;

  .bubble {
    position: absolute;
    border-radius: 50%;
    background: rgba($primary-color, 0.12);
    /*   */
    animation: subtleFloat 10s ease-in-out infinite alternate;
    /*     */
    filter: blur(5px);
    /*   */
    opacity: 0.8;
    /*   */

    &.bubble-1 {
      width: 400px;
      /*   */
      height: 400px;
      right: -150px;
      /*   */
      bottom: -150px;
      animation-duration: 12s;
      /*    */
      animation-delay: 0s;
    }

    &.bubble-2 {
      width: 300px;
      /*   */
      height: 300px;
      left: -100px;
      /*   */
      top: -100px;
      animation-duration: 15s;
      /*   */
      animation-delay: 2s;
      /*   */
    }
  }

  .golden-pattern {
    /*      */
    position: absolute;
    width: 500px;
    /*   */
    height: 500px;
    background: radial-gradient(circle, rgba($primary-color, 0.1) 0%, rgba(255, 255, 255, 0) 70%);
    /*    */
    right: -250px;
    top: -250px;
    opacity: 0.6;
    animation: continuousGlow 5s infinite alternate ease-in-out;
    /*     */
  }
}

/* ===============================================
   (Responsive Design)
=============================================== */
@media (max-width: 768px) {
  .auth-page {
    padding: 40px 0;
    /*     */
  }

  .auth-container {
    padding: 30px 15px;
    /*   */
  }

  .auth-card {
    padding: 30px 25px;
    /*    */
    border-radius: 15px;
    box-shadow: 0 8px 30px rgba(0, 0, 0, 0.6);

    &::before {
      height: 3px;
    }
  }

  .logo-container {
    margin-bottom: 30px;

    .logo {
      height: 80px;
      width: 80px;
    }

    .title {
      font-size: 1.8rem;

      &::after {
        height: 3px;
        bottom: -10px;
      }
    }
  }

  .form-title {
    font-size: 1.4rem;
    margin-bottom: 20px;

    &::after {
      height: 2px;
      bottom: -8px;
    }
  }

  .auth-form {
    gap: 18px;
  }

  .phone-input-wrapper,
  .auth-input .MuiInputBase-root {
    height: 52px !important;

    /*    */
    .form-control,
    input {
      font-size: 1rem !important;
      padding: 15px 12px !important;
    }
  }

  .auth-button {
    padding: 12px !important;
    font-size: 1rem !important;
    letter-spacing: 0.5px;
  }

  .back-button {
    top: 20px;
    #{$dir-left}: 20px;
    width: 45px;
    height: 45px;
    font-size: 1.5rem;
  }

  .auth-decoration {
    .bubble {
      filter: blur(3px);

      &.bubble-1 {
        width: 250px;
        height: 250px;
        right: -80px;
        bottom: -80px;
      }

      &.bubble-2 {
        width: 150px;
        height: 150px;
        left: -40px;
        top: -40px;
      }
    }

    .golden-pattern {
      width: 350px;
      height: 350px;
      right: -170px;
      top: -170px;
    }
  }
}

/* ===============================================
 RTL
=============================================== */
[dir="rtl"] {
  .auth-input .MuiFormHelperText-root {
    text-align: right;
    /*     */
  }

  .back-button {
    #{$dir-left}: auto;
    #{$dir-right}: 30px;
    /*     */
  }

  @media (max-width: 768px) {
    .back-button {
      #{$dir-right}: 20px;
      /*       */
    }
  }

  .phone-input-wrapper {
    direction: rtl;
    /*        */

    .react-tel-input {
      .form-control {
        padding-left: 14px;
        /*   */
        padding-right: 65px;
        /*   (  ) */
        text-align: right;
        /*    */
      }

      .flag-dropdown {
        border-right: 2px solid $border-color;
        /*   */
        border-left: none;
        /*     */
        border-radius: 0 10px 10px 0;
        /*    */
      }

      .country-list {
        .search-box {
          text-align: right;
          /*    */
        }
      }
    }

    .error-message-phone {
      text-align: right;
      /*     */
      margin-left: auto;
      /*    */
      margin-right: 10px;
      /*    */
    }
  }
}